import { newE2EPage } from '@stencil/core/testing';

describe('presto-search-bar', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-search-bar></presto-search-bar>');

    const element = await page.find('presto-search-bar');

    expect(element).toHaveClass('hydrated');
  });
});
